import React, { Component } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import HomeView from './views/Home';
import { CatalogView, ProductView } from './views/Catalog';
import NestedView from './views/Nested';
import Nav from './shared/Navbar';

const another = () => {
    return(
        <div>
            <h2> Yo! </h2>
        </div>
    )
}

function NotFound() {
    return(
        <div>
            <h1> 404 Not Found </h1>
            <p> <h3> The Page What Your Request Is Not Found </h3> </p>
        </div>
    );
};

class Indes extends Component {
    render() {
        return(
            <BrowserRouter>
                <div>    
                    <Nav />
                        <Switch>
                            <Route path="/" exact component={HomeView} />
                            <Route path="/another" component={another} />
                            <Route path="/catalog" component={CatalogView} />
                            <Route path="/product/:id" component={ProductView} />
                            <Route path="/nested" component={NestedView} />
                            <Route component={NotFound} /> 
                        </Switch>
                </div>
            </BrowserRouter>
        );
    }
}

export default Indes;